@keyframes slide-in-top {
  0% {
    transform: translateY(-200px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.phone-icon1 {
  position: relative;
  width: 36px;
  height: 35px;
}
.div34 {
  align-self: stretch;
  position: relative;
}
.div33 {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-black);
  width: 176px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-44xl) var(--padding-39xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xl);
  opacity: 0;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--color-white);
  font-family: var(--font-inter);
}
.div33.animate {
  animation: 1s ease 0s 1 normal forwards slide-in-top;
}
.div32 {
  position: relative;
  width: 100%;
  height: 209px;
  cursor: pointer;
}
